body {
  background-color: #F3F9FF;
}

.ncc-bg-copyright {
  background-color: #363131;
}

.ncc-white {
  color: #FFFDFD
}

.ncc-black {
  color: black
}

.ncc-bg-blue {
  background-color: rgb(33, 36, 135, 0.95);
}

.ncc-navBar {
  width: 100%;
}

.underline-white {
  border-bottom: 2px solid white;
}

.heading {
  text-align: center;
  font-size: 40px;
  font-weight: 600;
  letter-spacing: 0em;
  margin: 2rem;
}

.line {
  background-color: #053d4d;
  width: 30%;
  height: 4px;
}

.App{
  margin-top:161.5px;
}

@media only screen and (max-width: 470px) {
  .App {
      margin-top: 76px;
  }
}

@media only screen and (max-width: 1024px) {
  .heading {
    font-size: 35px;
    line-height: 35px;
  }

  .line {
    width: 25%;
    height: 3px;
  }
}

@media (max-width: 760px) {
  .heading {
    margin-bottom: 36px;
    font-weight: 600;
    font-size: 38px;
    line-height: 40px;
  }

  .line {
    width: 20%;
    height: 4px;
  }
  .App {
    margin-top: 97px;
}
}