#recentEventsCarousel .d-block {
    margin: auto;
    width: 70%;
    border-radius: 10px;
}

#recentEventsCarousel .carousel-item .block {
    box-sizing: border-box;
    margin: 0px;
    padding: 15px;
    position: relative;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    background-color: #222588;
    border-radius: 25px;
    width: 50%;
    font-size: 16px;
    font-family: Verdana, Tahoma, sans-serif;
    font-size: 1.6vw;
    color: #fff;
}

.modal-content {
    border: 0;
    display:flex;
    height:auto;
}

#recentEventsCarousel .carousel {
    margin-left: 7%;
    margin-right: 7%;
    margin-bottom: 0px;
}

#recentEventsCarousel .carousel:hover {
    cursor: pointer;
}

.line {
    background-color: #053d4d;
    width: 30%;
    height: 4px;
}

.recom {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 75%;
}

.flex-container {
    display: flex;
    align-items: center;
}

.flex-container>div {
    margin: 10px;
}

.recomblogs {
    background-color: #c5c7ca;
    height: 9%;
    width: 100%;
    overflow-x: scroll;
}

.tooltip {
    position: relative;
    display: inline-block;
    border-bottom: 1px dotted black;
}

.tooltip .tooltiptext {
    visibility: hidden;
    width: 120px;
    background-color: black;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;
    position: absolute;
    z-index: 1;
    bottom: 100%;
    left: 50%;
    margin-left: -60px;

    /* Fade in tooltip - takes 1 second to go from 0% to 100% opac: */
    opacity: 0;
    transition: opacity 1s;
}

.tooltip:hover .tooltiptext {
    visibility: visible;
    opacity: 1;
}

@media only screen and (max-width: 1024px) {
    .heading {
        font-size: 35px;
        line-height: 35px;
    }

    .line {
        width: 25%;
        height: 3px;
    }
}

@media (max-width: 760px) {
    .heading {
        margin-bottom: 36px;
        font-weight: 600;
        font-size: 38px;
        line-height: 40px;
    }

    .line {
        width: 20%;
        height: 4px;
    }

    #recentEventsCarousel .d-block {
        width: 50%;

    }
}

@media (max-width: 560px) {
    .block {
        box-sizing: border-box;
        margin: 0px;
        padding: 3px;
        position: relative;
        left: 50%;
        transform: translate(-50%, -50%);
        text-align: center;
        background-color: #222588;
        border-radius: 25px;
        width: 70%;
    }

    #recentEventsCarousel .d-block {
        width: 60%;
    }

}

@media (min-width: 720px) {
    #recentEventsCarousel .carousel {
        margin-left: 15%;
        margin-right: 15%;
        margin-bottom: 70px;
    }

    #recentEventsCarousel {
        width: 70%;
        margin: auto;
    }
}


@media (max-width:770px) {
    #recentEventsCarousel .carousel-container .card-container {
        width: 56%;
        flex-shrink: 0;
        height: 250px;
        padding-right: 15px;
        box-sizing: border-box;
    }
}

.recom .carousel-container .card-container img {
    width: 100%;
    border-radius: 10px;
}

#recentEventsCarousel .carousel-container.card-container .card {
    width: 100%;
    height: 100%;
    border: 1px solid #ccc;
    box-sizing: border-box;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
}

.recom .carousel-container {
    position: relative;
}

.event-nav button {
    border: 0;
    background-color: #f3f9ff;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
}

.nav button i {
    font-size: 30px;
}

.nav .prev {
    left: -40px;
    display: none;
}

.nav .prev.show {
    display: block;
}

.nav .next {
    right: -40px;
}

.nav .next.hide {
    display: none;
}

.card>* {
    flex: 1;
}

.card .img {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
}

.past-events .img img {
    aspect-ratio: 1/1;
    object-fit: cover;
}

.card .info {
    flex-basis: 40px;
    background: #222588;
    font-family: Verdana, Tahoma, sans-serif;
    color: #fff;
    flex-grow: 0;
    padding: 10px;
    box-sizing: border-box;
}



