/*-----------Modal css-------- */
.modal-event {
    background: #222588;
    color: white;
    justify-content: center;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    max-height: 100%;
}

.modal-dialog {
    max-width: 800px;
    /* min-height: auto; */
}

.modal-box {
    background: #DEE8F6;
    display: flex;
    width: 100%;
}

.event-name {
    font-family: Raleway;
    font-style: normal;
    font-weight: normal;
    font-size: 48px;
    line-height: 56px;
    text-align: center;
    padding-top: 20px;
}

.event-des {
    font-weight: 300;
    padding-bottom: 20px;
}

.event-details {
    margin-bottom: 4px;
    font-size: 12px;
}

@media only screen and (max-width: 760px) {
    .modal-box {
        flex-direction: column;
    }

    .modal-box div {
        width: 100%;
    }

    .modal-dialog {
        width: 80%;
    }

    .block {
        display:none;
    }
}

@media only screen and (max-width: 580px) {
    .modal-dialog {
        width: 60%;
        margin: 1rem auto;
    }

    .event-name {
        font-size: 28px;
        line-height: 40px;
        margin-bottom: 2px;
    }

    .event-des {
        text-align: start !important;
    }
}